import postRobot from '../../../post-robot-wrapper';
import './cc-number.html';
import '../styles/defaults.css';
import {handleEvent, handleReturnKeyEvent, runEventOnElement} from '../input-events';
import { applyOptions } from '../options';
import { getComponentIdFromQueryString, getControllerIdFromQueryString } from '../querystring';
import {stripLetters, checkToSendEvent} from '../utils.js';
import {config} from '../config';
import {
  mountComponent,
  addTriggerEvent,
  addHandleOptions,
  addGetComponentDataByValue
} from '../payment-events';
import {creditCardSpacesByBrand, formatLength, creditCardLengthByBrand} from '../credit-card-type';
import {generateComponentData} from '../payment-component-data';
import {removeExtraCharacters, spacesAdded} from '../utils';
import {getLocaleMessage} from '../localization/localized-messages';
import {getValueForComponentEvent, isMaskOptionTurnedOn, maskCreditCard} from '../masking';
import {setAriaLabel} from '../accessibility';

const COMPONENT_TYPE = 'cardnumber';

const componentData = generateComponentData(COMPONENT_TYPE,getComponentIdFromQueryString(COMPONENT_TYPE), getControllerIdFromQueryString());
componentData.removeWhiteSpace = true;

let defaultOptions = {
  placeholderText: getLocaleMessage('en-US', 'cardNumber')
};

const controllerListener = postRobot.listener({ window: componentData.controller.window, domain: config.domain });
const controllerEmitter = postRobot.client({ window: componentData.controller.window, domain: config.domain });

const triggerData = {
  componentData: componentData,
  getElement: getElement
};

mountComponent(controllerEmitter, componentData, handleOptions, emitComponentReady);
addHandleOptions(controllerListener, handleOptions);
addGetComponentDataByValue(controllerListener, getValue);
addTriggerEvent(controllerListener, runEventOnElement, triggerData);

export function getValue() {
  if (isMaskOptionTurnedOn(componentData)) {
    return componentData.storedValue;
  } else {
    return getElement().value;
  }
}

/**
 * getElement returns the ccNumber dom element
 * @returns {HTMLElement}
 */
export function getElement() {
  return document.getElementById('ccNumber');
}

/**
 * handleOptions applies options to the ccNumber dom element
 * @param {object} data
 */
export function handleOptions(data) {
  let el = getElement();

  defaultOptions.placeholderText = getLocaleMessage(data.instanceOptions.locale, 'cardNumber');
  applyOptions(el, data.options, defaultOptions);
  addInstanceOptions(data.instanceOptions);
  componentData.options = data.options;
  setAriaLabel(el, data.instanceOptions.locale, COMPONENT_TYPE);
}

/**
 * Stores the instance options in the component state
 * @param instanceOptions
 */
export function addInstanceOptions(instanceOptions) {
  componentData.instanceOptions = instanceOptions;
}


/**
 * handleBlur accepts a blur event and sends it on to handleEvent
 * @param {Event} event
 */
export function handleBlur(event) {
  handleEvent(componentData, 'blur', event);

  if (isMaskOptionTurnedOn(componentData)) {
    componentData.storedValue = event.target.value;
  }

  if (isMaskOptionTurnedOn(componentData) && event.target.value.length > 4) {
    const value = event.target.value;
    event.target.value = maskCreditCard(value);
  }
}

/**
 * handleFocus accepts a focus event and sends it on to handleEvent
 * @param {Event} event
 */
export function handleFocus(event) {
  handleEvent(componentData, 'focus', event);

  if (isMaskOptionTurnedOn(componentData) && typeof componentData.storedValue !== 'undefined') {
    const start = event.target.selectionStart; // NOTE: This is for IE 11
    const end = event.target.selectionEnd; // NOTE: This is for IE 11

    event.target.value = componentData.storedValue;

    event.target.selectionStart = start;
    event.target.selectionEnd = end;
  }

}

/**
 * setMaxLengthFromCurrentValueLength sets the maxLength for the ccNumber
 * field and removes characters beyond that length if the card brand is known
 */
function setMaxLengthFromCurrentValueLength() {
  if (componentData.prevState.brand !== 'UNKNOWN') {
    const el = getElement();

    const maxLength = creditCardLengthByBrand(componentData.prevState.brand) +
      creditCardSpacesByBrand(componentData.prevState.brand);

    el.value = removeExtraCharacters(el.value, maxLength);

  }
}

/**
 * keepSelectedPosition keeps the cursor in the intended position in the field
 * when spaces are added for formatting the card number
 * @param {number} initialPosition
 * @param {Event} event
 */
function keepSelectedPosition(initialPosition, event) {
  event.target.selectionStart = initialPosition;
  event.target.selectionEnd = initialPosition;
}

/**
 * formatNumber adds in spaces to format the text in the field appropriately
 * for the card brand when it is known
 * @param {Event} event
 */
function formatNumber(event) {
  if (componentData.prevState.brand && componentData.prevState.brand !== 'UNKNOWN') {
    const initialPosition = event.target.selectionStart;

    const number = formatLength(event.target.value, componentData.prevState.brand);
    const spacesAddedAmount = spacesAdded(event.target.value, number);

    event.target.value = number;
    keepSelectedPosition(initialPosition + spacesAddedAmount, event);
  }
}

/**
 * getComponentData returns componentData
 * @returns {{componentType: string, prevState: {}, controller: {id: (string|*)}, componentId: (string|*)}}
 */
export function getComponentData() {
  return componentData;
}

/**
 * handleChange accepts a change event, strips letters from the field value,
 * and if appropriate handles event, sets max length and applies space formatting
 * @param {Event} event
 */
export function handleChange(event) {
  const el = getElement();
  const hasFocus = document.activeElement === el;

  const initialEventValue = event.target.value;
  if (event.trigger !== 'showError') {
    event.target.value = stripLetters(event.target.value);
  }

  if(event.trigger === 'clear') {
    componentData.prevState = {
      empty: false,
      complete: false,
      error: null,
      brand: 'unknown'
    };
    componentData.storedValue = '';
    handleEvent(componentData, 'change', event, getValueForComponentEvent(event, componentData));
  } else if (checkToSendEvent(initialEventValue, event) && (hasFocus || event.trigger === 'showError')) {
    handleEvent(componentData, 'change', event, getValueForComponentEvent(event, componentData));

    formatNumber(event);

    setMaxLengthFromCurrentValueLength();
  }

}

/**
 * handleAutofill accepts an autofill event and sends it on to handleEvent
 * @param {Event} event
 */
export function handleAutofill(event) {
  handleEvent(componentData, 'autofill', event);
}

/**
 * emitComponentReady calls handleEvent with 'ready'
 */
export function emitComponentReady() {
  handleEvent(componentData, 'ready');
}

export function addInputHtmlToDom() {
  document.body.innerHTML += inputHtml();
}

export function keyUpEvent(event) {
  handleReturnKeyEvent(event, componentData);
}

export function inputHtml() {
  return `<input id="ccNumber"
         autocomplete="cc-number"
         class="base empty"
         type="tel"
         onfocus="handleFocus(event)"
         onblur="handleBlur(event)"
         oninput="handleChange(event)"
         onkeyup="keyUpEvent(event)"
         minLength="14"
         maxLength="30"
         value=""
         aria-invalid="false"
  />`;
}
